import { VacinaService } from './vacina.service';
import { Component, OnInit} from '@angular/core';
import { Observable } from 'rxjs';
import { cidadao } from './cidadao';

@Component({
  selector: 'app-vacina',
  templateUrl: './vacina.component.html',
  styleUrls: ['./vacina.component.css']
})
export class VacinaComponent implements OnInit {
  vacinasAplicadas: any;
  vacinasNaoAplicadas: any;
  vacinasFuturas: any;
  cidadao: cidadao;
  cidadao$: Observable<cidadao>;
  dependentes: any;
  cns: string;
  mostraDependente: boolean;

  constructor(private vacinaservice: VacinaService) { }

  openDialog() {
    console.log (this.cidadao);
    console.log (this.cidadao);
    this.mostraDependente = !this.mostraDependente;
    console.log (this.cidadao);
    
  }

  ngOnInit() {
    
   this.vacinaservice.listarVacinasAplicadas().subscribe(dados => this.vacinasAplicadas = dados);
   
   this.vacinaservice.listarVacinasNaoAplicadas().subscribe(dados => this.vacinasNaoAplicadas = dados);
   
   this.vacinaservice.listarVacinasFuturas().subscribe(dados => this.vacinasFuturas = dados);
  
   this.vacinaservice.buscarDependente().subscribe(dados => this.dependentes = dados);
   this.mostraDependente = false;
   this.vacinaservice.buscarCidadao().subscribe(d => this.cidadao=d);
   this.cidadao$ = this.vacinaservice.buscarCidadao();
   console.log (this.cidadao$);
  }
  getVacina() {
    return 'ok';
  }

}