import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { cidadao } from './cidadao';

@Injectable({
  providedIn: 'root'
})
export class VacinaService {
  private readonly API = 'http://www.sigas.treinamento.mg.gov.br/sigasPortal-frontend/rest/VacinaAi/';
  constructor(private http: HttpClient) { }

  listarVacinasAplicadas() {
    return this.http.get(this.API + 'buscar-cartao-vacina?cns=1234567');
  }

  listarVacinasNaoAplicadas() {
    return this.http.get(this.API + 'buscar-vacinas-pendentes?cns=1234567');
  }

  listarVacinasFuturas() {
    return this.http.get(this.API + 'buscar-proximas-vacinas?cns=1234567');
  }

  buscarCidadao() {
    return this.http.get<cidadao>(this.API + 'buscar-cidadao?cns=1234567');
  }

  buscarDependente() {
    return this.http.get(this.API + 'buscar-dependente?cns=1234567');
  }
}
